import React from "react";
import Modal from "react-modal";

export default function DeleteModel(props) {
  const [logoutmodalIsOpen, setLogoutIsOpen] = React.useState(
    props?.open ? true : false
  );
  const customStyles = {
    overlay: {
      zIndex: 12,
    },
    content: {
      top: "50%",
      left: "50%",
      right: "auto",
      bottom: "auto",
      marginRight: "-50%",
      transform: "translate(-50%, -50%)",
    },
  };

  const okHandle = () => {
    props?.deleteHandle();
    props?.open(false);
  };

  return (
    <Modal
      isOpen={logoutmodalIsOpen}
      onRequestClose={() => props?.open(false)}
      style={customStyles}
      contentLabel="Delete Modal"
      ariaHideApp={false}
    >
      <div className="p-10 max-w-[550px]">
        {!!props?.note && props?.note ? (
          <div className="flex-col text-black">
            <div className="w-full">Are you sure to delete it?</div>
            <div className="w-full mt-2">
              <span className="font-bold">Note:</span>
              <span className="ml-1">
                Make sure this room is not allocated to any conference data. If
                it has any conference data associated with this room will also
                be removed.
              </span>
            </div>
          </div>
        ) : (
          <p className="text-black">Are you sure to delete</p>
        )}

        <div className="flex-row flex mt-10 justify-between">
          <button
            type="submit"
            className="inline-flex  items-center justify-center h-[40px] text-[18px] rounded border bg-transparent border-primary px-4 py-2 text-sm font-bold text-primary shadow-sm focus:outline-none focus:ring-2 focus:ring-primary focus:ring-offset-2"
            onClick={() => props?.open(false)}
          >
            Cancel{" "}
          </button>
          <button
            type="submit"
            onClick={() => okHandle()}
            className="inline-flex  items-center justify-center h-[40px] text-[18px] rounded border bg-primary border-primary px-4 py-2 text-sm font-bold text-white shadow-sm hover:bg-primary focus:outline-none focus:ring-2 focus:ring-primary focus:ring-offset-2"
          >
            Ok{" "}
          </button>
        </div>
      </div>
    </Modal>
  );
}
